.imgcontainer {
  background-color: #2c3f52;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  height: 100%;
  width: 100%; }
  .imgcontainer:hover .img {
    transform: translateY(-5.625rem); }

.img {
  cursor: pointer;
  grid-column: 1/ 4;
  grid-row: 1/span 3;
  height: 100%;
  transition: transform 0.4s;
  width: 100%; }

.infocontainer {
  display: flex;
  flex-direction: column;
  grid-column: 1/ 4;
  grid-row: 3/span 3;
  padding: 0 1.25rem; }

.title {
  color: #fff;
  font-size: 1.5em;
  margin: 0; }

.description {
  color: #01c0bb; }

.description::before {
  content: 'by '; }

.buttoncontainer {
  align-items: center;
  display: flex;
  justify-content: space-between; }

.btn {
  background-color: #0057ff;
  border-radius: 0.125rem;
  border-width: 0;
  color: #fff;
  font-size: 1em;
  height: 1.875rem;
  margin: 0.3125rem;
  outline: none;
  padding: 0.3125rem;
  transition: background-color 0.3s; }
  .btn:hover {
    background-color: #0341bd; }
  .btn:focus {
    background-color: #0341bd; }



