.root {
  min-height: 100vh;
  padding: 0.3125rem 0 3.125rem 2.8125rem;
  position: absolute;
  width: 100%; }
  @media all and (max-width: 26.5625rem) {
    .root {
      padding-left: 0;
      padding-top: 3.125rem; } }

.grid {
  display: grid;
  grid-gap: 0.3125rem;
  grid-template-columns: repeat(auto-fill, minmax(18.125rem, 19.0625rem));
  grid-template-rows: repeat(auto-fill, minmax(13.125rem, 14.375rem));
  height: 100vh;
  justify-content: center;
  width: 100%; }



