.root {
  align-items: center;
  background-color: #02162b;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
  margin: 0;
  min-height: 31.25rem;
  padding: 1em 0;
  position: absolute;
  width: 3.125rem;
  z-index: 3; }
  @media all and (max-width: 26.5625rem) {
    .root {
      background-color: #02162b;
      flex-direction: row;
      height: 3.125rem;
      min-height: 3.125rem;
      width: 100%; } }

.logo {
  flex: 1; }
  @media all and (max-width: 26.5625rem) {
    .logo {
      display: none; } }

.social {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-end; }
  @media all and (max-width: 26.5625rem) {
    .social {
      display: none; } }

.link {
  display: flex;
  flex: 3;
  flex-direction: column;
  justify-content: center; }
  @media all and (max-width: 26.5625rem) {
    .link {
      flex-direction: row;
      font-size: 1.25rem;
      justify-content: space-around; } }

.icon {
  color: #fff;
  margin: 0.5em 0; }
  .icon:hover {
    color: #01c0bb; }
  .icon:active {
    color: #01c0bb; }



